body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #171923;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
    height: 16px;
    width: 16px;
    background-color: #171923;
}

::-webkit-scrollbar-thumb {
    background: #717277;
}

::-webkit-scrollbar-corner {
    background-color: #171923;
}
