@keyframes loading-rotation {
  from {
    transform: rotate(120deg);
  }
  to {
    transform: rotate(480deg);
  }
}

.loading-icon {
    line-height: 0;

    animation-iteration-count: infinite;
    animation-duration: 2s;
    animation-name: loading-rotation;
    animation-timing-function: ease-in-out;
}

